import React, { Component } from 'react';


import './our-showcase.scss'

import InstaService from '../../services/instaservice';

import Title from "../title";
import OurShowcaseFilter from "../our-showcase-filter";
import ProductList from "../product-list";
import ProductBlock from "../product-block";
import ErrorIndicator from "../error-indicator";

export default class OurShowcase extends Component {

	InstaService = new InstaService();

	state = {
		showcase: [],
		error: false
	};

	componentDidMount() {
		this.updateWatches();
	}

	updateWatches() {
		this.InstaService.getAllShowcase()
			.then(this.onShowcaseLoaded)
			.catch(this.onError)
	}

	onShowcaseLoaded = (showcase) => {
		this.setState({
			showcase,
			error: false
		})
	};

	onError = (error) => {
		this.setState({
			error: true
		})
	};

	isRenderRoute = (arr) => {
		if(arr.length !== 0) {
			return (
				arr.map((item, id) => {
					return <ProductList data={item}/>
				})
			);
		}
	};

	render() {

		const { showcase, error } = this.state;

		const item = error ? <ErrorIndicator/> : this.isRenderRoute(showcase);


		return (
			<ProductBlock classProps={`our-showcase`}>
				<div className="container">
					<Title title="Our Showcase" classMode="our-showcase__title"/>
					<OurShowcaseFilter/>
					{ item }
				</div>
			</ProductBlock>
		);
	}
}